<script lang="ts">
  import SketchSection from "./SketchSection.svelte";
  
  let song = "";

  const parse = (song) => {
    return song.split('\n').map(line => `<div>${line}</div>`).join("")
  }
</script>

<div class="sketch-container">
  <div class="input">
    <textarea bind:value={song} ></textarea>
  </div>

  <div class="output">
    {@html parse(song)}
  </div>
</div>

<style lang="scss">
  .sketch-container {
    display: flex;
    flex-direction: column;
    margin: 4rem auto;
  }

  .sketch {
